import React, { Component, Fragment } from 'react';
import { Link } from 'gatsby';
import Hamburger from '../Hamburger';
import Sidebar from '../Sidebar';
import './style.scss';
import * as baseActions from '../../state/modules/base';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { throttle } from 'lodash';
import {
  disableBodyScroll,
  enableBodyScroll,
  clearAllBodyScrollLocks,
} from 'body-scroll-lock';

class Header extends Component {
  targetElement = null;

  componentDidMount() {
    this.targetElement = document.querySelector(
      '.container',
    );
    // window.addEventListener('scroll', this.handleScroll);
  }

  showTargetElement = () => {
    // ... some logic to show target element

    console.log('3. Disable body scroll');
    console.log(this.targetElement);
    // 3. Disable body scroll
    disableBodyScroll(this.targetElement);
  };

  hideTargetElement = () => {
    // ... some logic to hide target element
    console.log('4. Re-enable body scroll');
    // 4. Re-enable body scroll
    enableBodyScroll(this.targetElement);
  };
  handleSidebar = evt => {
    const {
      visible,
      hideSidebar,
      showSidebar,
    } = this.props;
    // console.log('evt,', evt);
    if (visible) {
      hideSidebar();
      this.hideTargetElement();
    } else {
      this.showTargetElement();
      showSidebar();

      // evt.preventDefault();
    }
    // visible ? hideSidebar() : showSidebar();
  };
  // handleScroll = throttle(e => {
  //   const { visible } = this.props;
  //   if (visible) {
  //     console.log('important event ', e);
  //   } else {
  //     console.log('!!!!!!!!!!!!!!!!!!!!!');
  //   }
  // }, 500);

  componentWillUnmount() {
    // 5. Useful if we have called disableBodyScroll for multiple target elements,
    // and we just want a kill-switch to undo all that.
    // OR useful for if the `hideTargetElement()` function got circumvented eg. visitor
    // clicks a link which takes him/her to a different page within the app.
    clearAllBodyScrollLocks();
  }
  render() {
    const { handleSidebar } = this;
    const { visible } = this.props;
    console.log('this.props : ', this.props);
    return (
      <Fragment>
        <div className="header">
          <div className="header__title">
            <Link to="/">Sang Jeong</Link>
          </div>
          {/* <div className="header__menu">
            <Hamburger
              onOpen={handleSidebar}
              active={visible}
            />
          </div> */}
          <div className="header__sub-title">
            <Link to="/about">About</Link>
            <Link to="/about">Posts</Link>
            <Link to="/about">Tags</Link>
          </div>
        </div>
        <div className="fix-menu">
          <Hamburger
            onOpen={handleSidebar}
            active={visible}
          />
        </div>
        {visible && (
          <Sidebar
            outsideClickIgnoreClass={'fix-menu'}
            excludeScrollbar={true}
          />
        )}
      </Fragment>
    );
  }
}

export default connect(
  state => ({
    visible: state.base.visible,
  }),
  dispatch => ({
    BaseActions: bindActionCreators(baseActions, dispatch),
    hideSidebar: () => dispatch(baseActions.hideSidebar()),
    showSidebar: () => dispatch(baseActions.showSidebar()),
  }),
)(Header);
